html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #f5f0f0;
  font-family: "Monserrat", "sans-serif";
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.scroll-body {
  padding-top: 72.72px;
}

a {
  text-decoration: none;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1.4;
  text-align: justify;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 3rem;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  border-radius: 1rem;
}

.header {
  background-color: #2eada7;
}
@media (min-width: 768px) {
  .header.fijo {
    top: 0;
    position: fixed;
    width: 100%;
    background-color: #225f9c;
    box-shadow: 0.2rem 0.2rem 0.2rem rgb(70, 68, 68);
    border-radius: 0px 0px 8px 8px;
    -moz-border-radius: 0px 0px 8px 8px;
    -webkit-border-radius: 0px 0px 8px 8px;
    border: 0px solid #000000;
  }
}
.header h1 {
  color: #f5f0f0;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenedor-header {
  padding: 1.6rem;
}
@media (min-width: 768px) {
  .header .contenedor-header {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    justify-content: space-between;
    gap: 2.5rem;
  }
}
.navegacion a {
  display: block;
  text-align: center;
  color: #f5f0f0;
  margin-bottom: 1rem;
  font-weight: 600;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .navegacion a {
    margin: 0;
  }
}
.navegacion a:hover {
  color: #f5db33;
}

.video {
  position: relative;
  min-height: 40rem;
  max-height: 55rem;
  overflow: hidden;
}
@media (min-width: 768px) {
  .video {
    max-height: 65rem;
  }
}
.video .overlay-video {
  position: absolute;
  background: rgb(43, 37, 112);
  background: radial-gradient(circle, rgba(43, 37, 112, 0.4953703704) 20%, rgba(214, 39, 189, 0.2931066176) 88%);
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  margin: 0 auto;
  z-index: 1;
}
.contenido-video h2 {
  color: #f5f0f0;
  font-size: 6rem;
  margin: 0;
}
.contenido-video p {
  font-weight: 700;
  color: #f5f0f0;
  background-color: rgba(207, 23, 23, 0.8980392157);
  padding: 0.8rem 1.7rem;
  display: inline-block;
  border-radius: 3px;
  margin-top: 0;
}

@media (min-width: 768px) {
  .festival {
    padding: 4rem 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.festival .imagen-festival img {
  overflow: visible;
  -webkit-box-shadow: -15px 18px 28px -6px rgba(168, 62, 67, 0.83);
  -moz-box-shadow: -15px 18px 28px -6px rgba(168, 62, 67, 0.83);
  box-shadow: -15px 18px 28px -6px rgba(168, 62, 67, 0.83);
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival h2 {
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}
.contenido-festival .ubicaci√≥n {
  text-align: center;
  margin: 0;
  color: rgba(207, 23, 23, 0.8980392157);
  font-weight: 700;
  font-size: 2.1rem;
}

.lineup {
  background: rgb(248, 155, 48);
  background: -moz-radial-gradient(center, ellipse cover, rgb(248, 155, 48) 0%, rgb(246, 150, 51) 4%, rgb(210, 20, 121) 100%);
  background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgb(248, 155, 48)), color-stop(4%, rgb(246, 150, 51)), color-stop(100%, rgb(210, 20, 121)));
  background: -webkit-radial-gradient(center, ellipse cover, rgb(248, 155, 48) 0%, rgb(246, 150, 51) 4%, rgb(210, 20, 121) 100%);
  background: -o-radial-gradient(center, ellipse cover, rgb(248, 155, 48) 0%, rgb(246, 150, 51) 4%, rgb(210, 20, 121) 100%);
  background: -ms-radial-gradient(center, ellipse cover, rgb(248, 155, 48) 0%, rgb(246, 150, 51) 4%, rgb(210, 20, 121) 100%);
  background: radial-gradient(ellipse at center, rgb(248, 155, 48) 0%, rgb(246, 150, 51) 4%, rgb(210, 20, 121) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#f89b30", endColorstr="#d21479", GradientType=1 );
  padding: 4rem;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #f5f0f0;
}
.lineup .dia {
  text-transform: uppercase;
  margin-bottom: 3rem;
  font-weight: 600;
}

@media (min-width: 768px) {
  .escenarios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.escenarios p {
  color: #000;
  font-weight: 700;
  font-size: 2.4rem;
}
.escenarios p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
  margin-top: 1rem;
}

.rock .nombre-escenario:after {
  background-image: url("../../build/img/icono_rock.png");
}

.edm .nombre-escenario:after {
  background-image: url("../../build/img/icono_edm.png");
}

.calendario {
  margin: 0;
  padding: 0rem;
  list-style: none;
}
.calendario li {
  border-radius: 2.5px;
  padding: 2rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: normal;
}

.fondo-amarillo {
  background-color: #f5db33;
}
.fondo-amarillo li:nth-child(odd) {
  background-color: #eacc0b;
}

.fondo-verde {
  background-color: #2eada7;
}
.fondo-verde li:nth-child(odd) {
  background-color: #238580;
}

.escenario {
  -webkit-box-shadow: -17px -13px 63px -21px rgb(31, 26, 28);
  -moz-box-shadow: -17px -13px 63px -21px rgb(31, 26, 28);
  box-shadow: -17px -13px 63px -21px rgb(31, 26, 28);
  border-radius: 5px;
  padding: 5rem;
  margin-bottom: 3rem;
}

.galeria {
  padding: 3.5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0rem;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1.5rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 90%;
}
.overlay p {
  margin-top: -20rem;
  color: #f5f0f0;
  font-size: 2.6rem;
}

.boton {
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
  display: grid;
  place-items: center;
  background: rgb(20, 61, 213);
  background: linear-gradient(169deg, rgba(20, 61, 213, 0.6320903361) 21%, rgb(17, 17, 17) 93%);
  font-weight: 700;
}
.boton:hover {
  cursor: pointer;
}

.boletos {
  background: rgb(123, 228, 203);
  background: linear-gradient(169deg, rgb(120, 221, 198) 1%, rgb(58, 201, 169) 10%, rgb(12, 173, 136) 89%);
}
.boletos h2 {
  margin-bottom: 1rem;
  text-align: center;
  color: #f5f0f0;
  padding: 2.5rem;
}
.boletos .nombre-pase {
  color: #000;
  text-align: center;
}

.pases {
  width: 80%;
}
@media (min-width: 768px) {
  .pases {
    width: 95%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.pases h3 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.pase {
  padding-bottom: 2rem;
}
.pase .bg-blanco {
  border-radius: 8px;
  -webkit-box-shadow: -17px -13px 63px -21px rgb(31, 26, 28);
  -moz-box-shadow: -17px -13px 63px -21px rgb(31, 26, 28);
  box-shadow: -17px -13px 63px -21px rgb(31, 26, 28);
  background-color: #f5f0f0;
}
.pase-header p {
  margin-bottom: 0;
  border-radius: 8px 8px 0px 0px;
  -moz-border-radius: 8px 8px 0px 0px;
  -webkit-border-radius: 8px 8px 0px 0px;
  border: 0px solid #000000;
  background-color: rgba(207, 23, 23, 0.8980392157);
}
.pase-header p {
  padding: 0.4rem;
  color: #f5f0f0;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
}
.pase-body ul {
  margin: 0;
  padding: 0rem;
  list-style: none;
}
.pase-body li {
  text-align: center;
  padding: 1rem;
  border-bottom: 1.2px dotted rgba(61, 58, 58, 0.158);
}
.pase-footer {
  border-radius: 0px 0px 8px 8px;
  -moz-border-radius: 0px 0px 8px 8px;
  -webkit-border-radius: 0px 0px 8px 8px;
  border: 0px solid #000000;
  background-color: #752f97;
}
.pase-footer h3 {
  text-align: center;
  color: #f5f0f0;
  padding: 0.4rem;
}

.pag-footer {
  background-color: rgba(207, 23, 23, 0.8980392157);
}
.pag-footer p {
  margin: 0;
  padding: 1.2rem;
  text-align: center;
  font-weight: 700;
  color: #f5f0f0;
}