@use 'base/variables' as v;
@use 'base/mixins' as m;


.galeria {
  padding: 3.5rem 0;
  h3{
    text-align: center;
  }
}
.galeria-imagenes {
  @include m.lista;
  @include m.grid(3,1rem);

  @include m.tablet{
    @include m.grid (6, 1.5rem);
  }
}


.overlay {  
  position: fixed;
  top:0;
  left: 0;
  bottom: 0;
  width: 100%;
  height:100%;

  display: grid;
  place-items: center;
  background-color: rgb(0 0 0 / 50%);

 picture{
  display: block;
  max-width: 90%;
 
  
 }
 p{
  margin-top: -20rem;
  color: v.$blanco;
  font-size: 2.6rem;
}
}

.boton {
 width: 5rem;
 height: 5rem;
 border-radius: 100%;
 display: grid;
 place-items: center;
 background: rgb(20,61,213);
 background: linear-gradient(169deg, rgba(20,61,213,0.6320903361344538) 21%, rgba(17,17,17,1) 93%);
  font-weight: 700;

  &:hover{
    cursor: pointer;
  }
}
