
@use 'base/variables' as v;


@mixin phone {
  @media(min-width: v.$phone){
    @content;
  }
}
@mixin tablet {
  @media(min-width: v.$tablet){
    @content;
  }
}

@mixin pc{
  @media(min-width: v.$pc){
    @content;
  }
}

@mixin tv{
  @media(min-width: v.$monitor){
    @content;
  }
}


//display grid
@mixin grid($columnas,$gap){
  display:grid;
  grid-template-columns: repeat($columnas,1fr);
  gap:$gap;
}

@mixin fondo-lineup(){
  background: rgba(248,155,48,1);
  background: -moz-radial-gradient(center, ellipse cover, rgba(248,155,48,1) 0%, rgba(246,150,51,1) 4%, rgba(210,20,121,1) 100%);
  background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(248,155,48,1)), color-stop(4%, rgba(246,150,51,1)), color-stop(100%, rgba(210,20,121,1)));
  background: -webkit-radial-gradient(center, ellipse cover, rgba(248,155,48,1) 0%, rgba(246,150,51,1) 4%, rgba(210,20,121,1) 100%);
  background: -o-radial-gradient(center, ellipse cover, rgba(248,155,48,1) 0%, rgba(246,150,51,1) 4%, rgba(210,20,121,1) 100%);
  background: -ms-radial-gradient(center, ellipse cover, rgba(248,155,48,1) 0%, rgba(246,150,51,1) 4%, rgba(210,20,121,1) 100%);
  background: radial-gradient(ellipse at center, rgba(248,155,48,1) 0%, rgba(246,150,51,1) 4%, rgba(210,20,121,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f89b30', endColorstr='#d21479', GradientType=1 );
}
@mixin gradient-imagen{
  -webkit-box-shadow: -15px 18px 28px -6px rgba(168,62,67,0.83);
  -moz-box-shadow: -15px 18px 28px -6px rgba(168,62,67,0.83);
  box-shadow: -15px 18px 28px -6px rgba(168,62,67,0.83);
}

@mixin lista {
  margin:0;
  padding:0rem;
  list-style: none;
}

@mixin sombra{
  -webkit-box-shadow: -17px -13px 63px -21px rgba(31,26,28,1);
  -moz-box-shadow: -17px -13px 63px -21px rgba(31,26,28,1);
  box-shadow: -17px -13px 63px -21px rgba(31,26,28,1);
}
@mixin centrar{
  text-align: center;
}

@mixin border-superior {
  border-radius: 8px 8px 0px 0px;
-moz-border-radius: 8px 8px 0px 0px;
-webkit-border-radius: 8px 8px 0px 0px;
border: 0px solid #000000;
}
@mixin border-inferior {
  border-radius: 0px 0px 8px 8px;
-moz-border-radius: 0px 0px 8px 8px;
-webkit-border-radius: 0px 0px 8px 8px;
border: 0px solid #000000;
}
