@use 'base/variables' as v;
@use 'base/mixins' as m;


.lineup {
  @include m.fondo-lineup();
  padding:4rem;
  h3,p {
    text-align: center;
    color:v.$blanco;  
  }
  .dia{
    text-transform: uppercase;
    margin-bottom: 3rem;
    font-weight: 600;
  }
}

.escenarios {
  @include m.tablet{  
    @include m.grid(2, 4rem);
  }
 p {
  color: v.$negro;
  font-weight: 700;
  font-size: 2.4rem;
  
 }
  p{
    &:after {
    content:"" ;
    display: block;
    width: 20rem;
    height: 10rem;
    background-size: 15rem; 
    background-repeat: no-repeat;
    background-position: center center;
    margin:0 auto;
    margin-top: 1rem;
  }
 
}
  
}

.rock .nombre-escenario:after {
  background-image: url('../../build/img/icono_rock.png');
}
.edm .nombre-escenario:after{
  background-image: url("../../build/img/icono_edm.png");
}
.calendario {
  @include m.lista;
  li{
    border-radius: 2.5px;
    padding: 2rem;
    font-weight: 700;
    span{
     font-weight: normal;
    }
  }
}

.fondo-amarillo {
 background-color: v.$amarillo;
 li:nth-child(odd){
  background-color: darken(v.$amarillo,10%);
 }
}
.fondo-verde {
 background-color: v.$verde;
 li:nth-child(odd){
  background-color: darken(v.$verde,10%);
 }
}
.escenario {
 @include m.sombra;
  border-radius: 5px;
  padding: 5rem;
  margin-bottom: 3rem;
}