@use 'variables' as v;

html{
  font-size: 62.5%;
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: v.$blanco;
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
  &.fijar-body{
    overflow: hidden;
  }
  &.scroll-body {
    padding-top: 72.72px;
  }

}
a {
  text-decoration: none;
}
p{
  font-size: 2rem;
  color: v.$negro;
  line-height: 1.4;
  text-align: justify;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
}
h1,h2,h3 {
  margin:0 0 3rem;
}

h1 {
  font-size: 5rem;
}
h2 {
  font-size: 4rem;
}
h3 {
  font-size: 3rem;
}

img {
  max-width:100%;
  width: 100%;
  height: auto;
  display: block;
  border-radius: 1rem;
}