@use 'base/variables' as v;
@use 'base/mixins' as m;


.header{
  background-color: v.$verde;
  @include m.tablet{
     
  
  &.fijo{
    top: 0;
    position: fixed;
    width: 100%;
    background-color: #225f9c;
    box-shadow: .2rem .2rem 0.2rem rgb(70, 68, 68);
    @include m.border-inferior;
    
  }
  }
 
 
  h1{
    color:v.$blanco;
    text-align: center;

   @include m.tablet{
   margin:0;
   }
     
  }
  .contenedor-header {
    padding:1.6rem;
    @include m.tablet{
      display:flex;
      justify-content: space-between;
      gap:2rem;
      align-items: center;
    }
  }
}

.navegacion {
  
  @include m.tablet{
    display:flex;
    justify-content: space-between;
    gap:2.5rem;
    }
  a{
    display: block;
    text-align: center;
    color:v.$blanco;
    margin-bottom: 1rem;
    font-weight: 600;
    font-size: 3rem;
    @include m.tablet{
      margin:0 ;

    }
    
    
    &:hover {
      color:v.$amarillo;
    }
  }
}






