
@use 'base/variables' as v;
@use 'base/mixins' as m;


.boletos {
  background: rgb(123, 228, 203);
  background: linear-gradient(169deg, rgb(120, 221, 198) 1%, rgba(58,201,169,1) 10%, rgba(12,173,136,1) 89%);
  h2 {
    margin-bottom: 1rem;
    @include m.centrar;
    color: v.$blanco;
    padding: 2.5rem;
  }
  .nombre-pase{
    color: v.$negro;
   @include m.centrar;
  
  }
}

.pases {
  width:80%;
  @include m.tablet{
    width: 95%;
    @include m.grid(2,3rem); 
  }
  h3{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
}

.pase{
padding-bottom: 2rem;
  .bg-blanco {
    border-radius: 8px;
    @include m.sombra;
    background-color: v.$blanco;
  }
  &-header {
    p{
      margin-bottom: 0;
      @include m.border-superior;
      background-color: v.$rojo;
    }
  
  p{
    padding: .4rem;
    color: v.$blanco;
    @include m.centrar;
    text-transform: uppercase;
    font-weight: 700;
  }}

  &-body{
    ul{
    @include m.lista;
    }
      li{
        text-align: center;
        padding: 1rem;
        border-bottom: 1.2px dotted rgba(61, 58, 58, 0.158);
      }
    }
 
  &-footer {
    @include m.border-inferior;
    background-color: v.$morado;
    h3{
      @include m.centrar;
      color: v.$blanco;
      padding: .4rem;
    }
   
  
}
}