@use 'base/mixins' as m;
@use 'base/variables' as v;

.video {
  position: relative;
  min-height: 40rem;
  max-height: 55rem;
  overflow: hidden;
  @include m.tablet{
    max-height: 65rem;
  }
  
  .overlay-video{
    position: absolute;
    background: rgb(43,37,112);
    background: radial-gradient(circle, rgba(43,37,112,0.49537037037037035) 20%, rgba(214,39,189,0.2931066176470588) 88%);
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
   
  }
  video {
    width: auto;
    display: block;
    @include m.tablet{
      width: 100%;  
    }

  }
  
}
.contenido-video {
  margin:0 auto;
  z-index: 1;
  h2
  {
    color:v.$blanco;
    font-size: 6rem;
    margin:0;
  }
  p{
    font-weight: 700;
    color:v.$blanco;
    background-color: v.$rojo;
    padding: .8rem 1.7rem;
    display: inline-block;
    border-radius: 3px;
    margin-top: 0;
   
  }
}